import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {  AlertController, ToastController } from '@ionic/angular';
import { HttpService } from 'src/app/services/http.service';
import { StorageService } from 'src/app/services/storage.service';
import { AuthConstants } from 'src/app/configuration/auth-constants';
import { IUser } from 'src/app/models/IUser';
import { FormValidationService } from 'src/app/services/form-validation.service';

@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.page.html',
  styleUrls: ['./user-detail.page.scss'],
})
export class UserDetailPage implements OnInit {

  user: IUser;
  loggedUser: IUser;
  data_loaded: boolean = false;
  token: string;
  userId: string;
  newUser: boolean;
  confirPass: string;
  invalidInput: {
    user: string,
    password: string,
    confirPass: string,
    firstName: string,
    lastName: string
  };

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    public toastController: ToastController,
    private httpClient: HttpService,
    private storageService: StorageService,
    private alertController: AlertController,
    private formValidations: FormValidationService
  ) { }

  ngOnInit() {
    this.storageService.get(AuthConstants.USER).then(res=>{
      console.log(res);
      this.loggedUser = res.user;
      this.token = res.accessToken;
    });
    this.confirPass = ''
    this.invalidInput = {
      user: '',
      password: '',
      confirPass: '',
      firstName: '',
      lastName: ''
    };
    this.userId =  this.activatedRoute.snapshot.paramMap.get('id');
    if (this.userId === 'new') {
      this.newUser = true;
      this.user = {
        firstName: '',
        lastName: '',
        user: '',
        password: '',
        level: 1,
        groupLevel: 0,
        passExpirationDate: new Date(),
        active: true,
        erased: false,
      }
    } else {
      this.newUser = false;
    }
  }

  ionViewWillEnter(){
    if (this.newUser === false) {
      this.httpClient.getUser(this.userId, this.token)
        .subscribe(user => {
          console.log(user);
          this.user = user;
          this.data_loaded = true;
          this.user.password = '***************';
        }, error => this.showAlert(error))
    }
  }

  // LOCAL PARA CADA FORM
  trimInputs() {
    this.user.user.trim();
    this.user.firstName.trim();
    this.user.lastName.trim();
    this.user.password.trim();
  }

  async saveUser() {
    
    // SACA LOS ESPACIOS EN BLANCO DE LAS PROPIEDADES DEL USUARIO ( A TODES )
    this.trimInputs();

    // VALIDA LOS ELEMENTOS PASADOS COMO STATE CON LAS REGLAS DE VALIDACION APLICADAS
    let validated = {};
    if ( this.newUser === true ) {
      validated = this.formValidations.validate(
        { 
          ...this.user,
          confirPass: this.confirPass
        }, 
        [
          { param1: 'user', param2: 2, val: ['min-length'] },
          { param1: 'firstName', param2: 2, val: ['min-length'] },
          { param1: 'lastName', param2: 2, val: ['min-length'] },
          { param1: 'password', val: ['empty', 'pass'] },
          { param1: 'confirPass', param2: 'password', val: [ 'empty', 'are-equal'] }
        ]
      );
    }
    else {
      validated = this.formValidations.validate(
        { 
          ...this.user,
          confirPass: this.confirPass
        }, 
        [
          { param1: 'user', param2: 2, val: ['min-length'] },
          { param1: 'firstName', param2: 2, val: ['min-length'] },
          { param1: 'lastName', param2: 2, val: ['min-length'] },
        ]
      );
    }
    console.log(validated)
    this.invalidInput = {
      ...this.invalidInput,
      ...validated
    }

    // CORROBORA POR ALGUN ELEMENTO CON STRING DE ERROR
    let invalid = false
    for ( let error in this.invalidInput ) {
      if ( this.invalidInput[error] !== '' ) invalid = true;
    }
    if ( !invalid ) {
      // NO HAY ERRORES
      const alert = await this.alertController.create({
        header:'Atención!',
        message: `¿Confirma que desea ${ this.newUser ? 'crear' : 'modificar'} el usuario ${this.user.firstName} ${this.user.lastName}?`,
        buttons: [
          {
            text:'No',
            role:'cancel',
            handler: ()=>{}
          },
          {
            text:'Si',
            handler: () => {
              if ( this.newUser === true ) {
                this.httpClient.createUser(this.user, this.token)
                  .subscribe( res => {
                    this.goBackToList();
                  }, 
                  error => this.showAlert(error)
                )
              }
              else {
                this.httpClient.updateUser(this.user, this.token)
                  .subscribe( res => {
                    this.goBackToList();
                  }, 
                  error => this.showAlert(error)
                );
              }
            }
          }
        ]
      });
      await alert.present();
    }
  }
//   else {
//     this.httpClient.updateUser(this.user, this.token)
//       .subscribe( res => {
//         this.goBackToList();
//       }, error => this.showAlert(error));
//     }
// };

  async deleteUser() {
    const alert = await this.alertController.create({
      header:'Atención!',
      message: `¿Confirma que desea borrar al usuario ${this.user.firstName} ${this.user.lastName}?`,
      buttons: [
        {
          text:'No',
          role:'cancel',
          handler: ()=>{}
      },{
          text:'Si',
          handler: () => {
            this.httpClient.deleteUser(+this.userId, this.token)
              .subscribe( res => {
                this.goBackToList();
              }, error => {
                console.log(error);
                this.showAlert(error);
              });
          }
      }]
    });
    await alert.present();
  };

  clearInvalidInput(field: string) {
    this.invalidInput[field] = ''
  }

  async showAlert(msg: string){
    const alert = await this.alertController.create({
      header:'Atención!',
      message: `${msg}`,
      buttons: [
        {
          text:'OK',
          handler: ()=>{}
      }]
    });
    await alert.present();
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Atrás' : 'Atrás';
  };

  goBackToList(): void {
    this.router.navigate(['/users']);
  }
};



